import React from "react";
import  './Nouns.css'
import {Route, Switch} from "react-router";

function Nouns(props) {

    return (
        <div className={'nouns'}>
           <h1>Nouns</h1>
            <Switch>
                <Route exact path={'/nouns/learned'} component={Learned}/>
            </Switch>
        </div>
    )
}

function Learned(props) {
    return <h1>Learned</h1>
}

export default Nouns;